import Vue from 'vue'
import Router from 'vue-router'
import Upload from '@/components/Upload'
import SystemMgm from '@/components/SystemMgm'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/upload',
      name: 'Upload',
      component: Upload
    },
    {
      path: '/systems',
      name: 'Systems',
      component: SystemMgm
    }
  ]
})



